import React from 'react';
import { arrayOf, string, shape } from 'react-proptypes';

import styles from './Features.module.scss';
import { Skill } from '../../../components/Skill';

const Features = ({ params }) => (
    <div className={styles.features__container} >
        <h2 className="small">
            {params && params.title}
        </h2>

        <div className={styles.items__container} >
            {
                params && params.items && params.items.map((item, index) => Skill(item, index))
            }
        </div>
    </div>
);

Features.propTypes = {
    params: shape({
        title: string,
        items: arrayOf(shape({
            title: string,
            icon: string,
            color: string,
            description: string
        }))
    })
};

Features.defaultProps = {
    params: {
        items: []
    }
};

export default Features;



